#hero {
  height: 88vh;
  display: flex;
  padding-left: 0 !important;
  align-items: center;
  border-bottom: 0px;
  background: $white-color;
  /*  -webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%); */
  font-weight: 400;
  color: $dark-blue-text;
  padding: 0rem 5.6rem;
  margin-bottom: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;

  @include respond(phone) {
    padding: 0 0;
  }

  .hero-title {
    font-size: 3.2rem;
    font-weight: 700;
    margin-bottom: 3.2rem;
    text-align: left;

    @include respond(tab-land) {
      font-size: 2.5rem;
    }
    @include respond(tab-port) {
      font-size: 2.5rem;
      text-align: center;
    }
    @include respond(phone) {
      font-size: 1.6rem;
      line-height: 1.5;
    }
    @include respond(phone-xs) {
      font-size: 1rem;
    }
  }

  .hero-cta {
    display: flex;
    margin-top: 4rem;
    @include respond(tab-port) {
      justify-content: center;
    }

    & a {
      font-size: 1.4rem;

      @include respond(phone) {
        font-size: 1rem;
      }
    }
  }

  .container {
    margin-top: 8rem;
  }
}
